import React from "react";

const ChatBar = (props) => {
    const [state, setState] = React.useState([
        { id: 1, image: "/images/Balasaheb.jpg", name: "Baalasaheb Thackeary" },
        { id: 2, image: "/images/Uddhav.jpg", name: "Uddhav Thackeray" },
        { id: 3, image: "/images/Allu arjun.jpg", name: "Allu Arjun" },
        { id: 4, image: "/images/Ramcharan.jpg", name: "Ram Charan" },
        { id: 5, image: "/images/Ambani.jpg", name: "Mukesh Ambani" },
        { id: 6, image: "/images/Markzuckerberg.jpg", name: "MarkZuckerberg" },
        { id: 7, image: "/images/Jeff.jpg", name: "Jeff Bezos" },
        { id: 8, image: "/images/ElonMusk.jpg", name: "Elon Musk" },
        { id: 9, image: "/images/modi.jpg", name: "Narendra Modi" },
        { id: 10, image: "/images/Raj.jpg", name: "Raj Thackeray" },

  
    ]);
    const openChat = (user) => {
        props.openChat(user);
    };
    return (
        <div className="chatBar">
            {state.map((user) => (
                <div
                    className="chatBar__list"
                    key={user.id}
                    onClick={() => openChat(user)}
                >
                    <div className="chatBar__list-img">
                        <img src={user.image} alt="user" />
                        <span className="status"></span>
                    </div>
                    <div className="chatBar__list-name">{user.name}</div>
                </div>
            ))}
        </div>
    );
};

export default ChatBar;