import React from 'react'

const Stories = () => {
    const [state, setstate] = React.useState([
        { id: 1, image: '/images/Balasaheb.jpg', name: 'Balasaheb ' },
        { id: 2, image: '/images/modi.jpg', name: 'modi ' },
        { id: 3, image: '/images/Uddhav.jpg', name: 'Uddhav ' },
        { id: 4, image: '/images/Raj.jpg', name: 'Raj ' },
    ])
    return (
        <div className="stories">

            {state.map(story => (
                <div className="stories__col" key={story.id}>
                    <div className="stories__body">
                        <img src={story.image} alt="stories" />
                        <div className="stories__body-overlay">
                            <div className="stories_body-overlay-img">
                                <img src={story.image} alt="" />
                            </div>
                            <div className="stories__body-name">
                                {story.name}
                            </div>
                        </div>
                    </div>
                </div>
            ))}
        </div>
    )
}

export default Stories;
