import React from "react";
import { FaRegThumbsUp, FaRegCommentAlt, FaShareAlt } from "react-icons/fa";
const ShowPost = () => {
    const [state, setState] = React.useState([
        {
            id: 1,
            userImg: "/images/Balasaheb.jpg",
            name: "Baalasaheb Thackeray",
            time: "2h",
            text:
                `Bal Keshav Thackeray, lovingly addressed as "Balasaheb", was an Indian politician who founded the Shiv Sena, a right-wing pro-Marathi and Hindu nationalist party active mainly in the state of Maharashtra.`,
            postImg: "/images/Baalasahebpost.jpg",
        },
        {
            id: 2,
            userImg: "/images/modi.jpg",
            name: "Narendra Modi",
            time: "4h",
            text:
                `Narendra Damodardas Modi is an Indian politician serving as the 14th and current prime minister of India since 2014. Modi was the chief minister of Gujarat from 2001 to 2014 and is the Member of Parliament from Varanasi. He is a member of the Bharatiya Janata Party and its National Democratic Alliance.`,
            postImg: "/images/Modipost.jpg",
        },
        {
            id: 3,
            userImg: "/images/Uddhav.jpg",
            name: "Uddhav Thackeray",
            time: "2h",
            text:
                `Uddhav Balasaheb Thackeray is an Indian politician who is the 19th and current Chief Minister of Maharashtra. He is also the president of Shiv Sena.`,
            postImg: "/images/Uddhavpost.jpg",
        },
        {
            id: 4,
            userImg: "/images/Raj.jpg",
            name: "Raj Thackeray",
            time: "2h",
            text:
                `Raj Shrikant Thackeray is an Indian politician and chairperson of the Maharashtra Navnirman Sena (MNS), a regional political party. He is the nephew of Bal Thackeray; and a cousin of Shiv Sena chief and the 19th and current Chief Minister of Maharashtra Uddhav Thackeray.`,
            postImg: "/images/Rajpost.jpg",
        },
    ]);
    return (
        <div className="show">
            {state.map((post) => (
                <div key={post.id} className="empty">
                    <div className="show__header">
                        <div className="show__header-img">
                            <img src={post.userImg} alt="user" />
                        </div>
                        <div className="show__header-name">
                            {post.name} <div className="date">{post.time}</div>
                        </div>
                    </div>
                    <div className="show__body">
                        <div className="show__body-text">{post.text}</div>
                        <div className="show__body-img">
                            <img src={post.postImg} alt="post" />
                        </div>
                    </div>
                    <div className="show__reactions">
                        <span className="reactions">
                            <FaRegThumbsUp /> <span className="reactions-text">Like</span>
                        </span>
                        <span className="reactions">
                            <FaRegCommentAlt />{" "}
                            <span className="reactions-text">Comments</span>
                        </span>
                        <span className="reactions">
                            <FaShareAlt /> <span className="reactions-text">Share</span>
                        </span>
                    </div>
                </div>
            ))}
        </div>
    );
};

export default ShowPost;