import React from 'react';
import { useState } from 'react';

const Sidebar = () => {
    const [state, setstate] = useState([
        { id: 1, image: '/images/Balasaheb.jpg', name: 'Balasaheb posts' },
        { id: 2, image: '/images/modi.jpg', name: 'Modi posts' },
        { id: 3, image: '/images/Uddhav.jpg', name: 'Uddhav posts' },
        { id: 4, image: "/images/Raj.jpg", name: "Raj Thackeray posts" },
        { id: 5, image: '/images/Devendra.jpg', name: 'Devendra posts' },
        { id: 6, image: '/images/Aditya.jpg', name: 'Aditya posts' },
        { id: 7, image: "/images/Jeff.jpg", name: "Jeff Bezos posts" },
        { id: 8, image: "/images/ElonMusk.jpg", name: "Elon Musk posts" },
        { id: 9, image: "/images/modi.jpg", name: "Narendra Modi posts" },
  
    ])
    return (
        <div className="sidebar">
            {state.map(info => (
                <div className="sidebar__list" key = {info.id}>
                    <div className="sidebar__list-img">
                        <img src={info.image} alt="Posts" />
                    </div>
                    <div className="sidebar__list-name">
                        {info.name}
                    </div>
                </div>
            ))}
        </div>
    )
}

export default Sidebar
